import React, { useCallback, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import _ from 'lodash';
import moment from 'moment';

import {
  RowDownIcon,
  RightDownIcon,
  EyeIcon,
  EyeHideIcon,
} from 'src/assets/icons';

import { ButtonCustom } from 'src/components/base';
import './itemModalVoucherHistory.scss';
import { isExpiredDate } from 'src/utils/dateTime';

const ItemChild = React.memo(({ data, onClickDelete, onClickUse }) => {
  console.log('🚀 ~ file: index.js ~ line 31 ~ ItemChild ~ data', data);
  const [showIconEye, setIconEye] = useState(false);
  const handleShowHide = useCallback(() => {
    setIconEye(!showIconEye);
  }, [showIconEye]);

  const code = _.get(data, 'code');
  const codeLength = code.length;
  const expired_at = moment(_.get(data, 'expired_at')).format('DD/MM/yyyy');
  const expired_time = moment(_.get(data, 'expired_at')).format('HH:mm:ss');

  const textHidden = code.substring(codeLength, codeLength - 3);

  const handleDeleteVoucher = () => {
    if (onClickDelete && typeof onClickDelete === 'function') {
      onClickDelete(data);
    }
  };

  const handleUseVoucher = () => {
    if (onClickUse && typeof onClickUse === 'function') {
      onClickUse(data);
    }
  };

  return (
    <div className="item-voucher-modal-container">
      <Grid container className="contain">
        <div container className="line" />
        <Grid className="left-contain">
          <p className="text-expiry-date">HSD: {expired_at}</p>
          <Grid container className="box-code">
            <p className="text-code">
              {showIconEye ? code : `*******${textHidden}`}
            </p>
            {showIconEye ? (
              <EyeIcon onClick={handleShowHide} />
            ) : (
              <EyeHideIcon onClick={handleShowHide} />
            )}
          </Grid>
          <p className="text-expiry-date">{expired_time}</p>
        </Grid>
        <Grid className="right-contain">
          <ButtonCustom
            item
            onClick={handleDeleteVoucher}
            title="Xóa"
            customStyle="custom-button-style delete"
            customTitle="custom-button-title delete"
          />
          <ButtonCustom
            item
            onClick={handleUseVoucher}
            title="Sử dụng"
            customStyle="custom-button-style use"
            customTitle="custom-button-title use"
          />
        </Grid>
      </Grid>
    </div>
  );
});

const ItemVoucherHistory = ({
  data,
  onClickDeleteVoucher = () => {},
  onClickUseVoucher = () => {},
}) => {
  const [showIconRow, setIconRow] = useState(true);
  const handleShowHide = useCallback(() => {
    setIconRow(!showIconRow);
  }, [showIconRow]);

  // const dataParser = data.map((obj, idx) => {
  //   return {
  //     ...obj,
  //     create_at_parser: moment(_.get(obj, 'created_at')).format('DD/MM/YYYY'),
  //   };
  // });
  // const groupByDate = _.groupBy(dataParser, 'create_at_parser');

  return (
    <div className="item-voucher-modal-container">
      <Grid container className="box-date" onClick={handleShowHide}>
        <p className="text-date">
          Ngày lấy mã: {_.get(data, '[0].create_at_parser')}
        </p>
        {showIconRow ? <RowDownIcon /> : <RightDownIcon />}
      </Grid>

      {showIconRow &&
        data &&
        data.map((obj) => {
          if (!obj.is_used && !isExpiredDate(obj.expired_at)) {
            return (
              <ItemChild
                data={obj}
                key={obj?.id}
                onClickDelete={onClickDeleteVoucher}
                onClickUse={onClickUseVoucher}
              />
            );
          }
        })}
    </div>
  );
};

export default React.memo(ItemVoucherHistory);
