import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import ServiceView from './serviceHistory.view';

const ServiceContainer = () => {
  const listBanner = useSelector(getListBannerSelector);
  return <ServiceView listBanner={listBanner} />;
};

export default ServiceContainer;
