import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { getCodeRequiredHistoryHandle } from 'src/redux/actions/voucher.action';

import CodeRequired from './codeRequired.view';

const CodeRequiredContainer = () => {
  const dispatch = useDispatch();

  const [listCodeRequired, setListCodeRequired] = useState([]);
  useEffect(() => {
    dispatch(
      getCodeRequiredHistoryHandle({
        onSuccess: (listHotDeal) => {
          setListCodeRequired(listHotDeal);
        },
      })
    );
  }, []);

  return <CodeRequired listCodeRequired={listCodeRequired} />;
};

export default CodeRequiredContainer;
