import CodeRequired from '../codeRequired/codeRequired.container';

import './serviceHistory.scss';
import DeliveryHistoryContainer from '../deliveryHistory/deliveryHistory.container';

/**
 * Modal: history use voucher
 */
const HistoryUseVoucherModal = () => {
  const dispatch = useDispatch();
  const [listUsedVoucher, setListUsedVoucher] = useState([]);
  useEffect(() => {
    dispatch(
      getListVoucherHistoryHandle({
        onSuccess: (listVoucherRes) => {
          console.log('listUsedVoucherTemp', listVoucherRes);
          const listUsedVoucherTemp = listVoucherRes.filter((obj) => {
            return (
              obj.is_used ||
              (!obj.is_used &&
                moment(obj.expired_at).valueOf() <= moment().valueOf())
            );
          });
          setListUsedVoucher(listUsedVoucherTemp);
        },
      })
    );
  }, []);
  console.log('listUsedVoucher', listUsedVoucher);
  return (
    <div className="history-voucher-modal-container">
      <ClockGrayIcon />
      <p className="title">Lịch sử sử dụng Voucher</p>
      <div className="list_voucher_container">
        {listUsedVoucher.map((obj, idx) => {
          console.log(
            'listUsedVoucher',
            obj.expired_at,
            moment(obj.expired_at).valueOf(),
            moment().valueOf()
          );
          if (moment(obj.expired_at).valueOf() <= moment().valueOf()) {
            return (
              <div key={idx} className="box-item">
                <div className="box-icon-check">
                  <ErrorIcon icon="check" />
                </div>
                <div>
                  <p className="title-voucher">{`Hết hạn voucher ${obj.deal.name}`}</p>
                  <p className="name-voucher">{obj.deal.merchant.name}</p>
                  <p className="date-voucher">
                    {moment(obj.updated_at).format('HH:mm DD/MM/YYYY')}
                  </p>
                </div>
              </div>
            );
          }
          return (
            <div key={idx} className="box-item">
              <div className="box-icon-check">
                <CheckGreenIcon />
              </div>
              <div>
                <p className="title-voucher">{`Sử dụng voucher ${obj.deal.name}`}</p>
                <p className="name-voucher">{obj.deal.merchant.name}</p>
                <p className="date-voucher">
                  {moment(obj.updated_at).format('HH:mm DD/MM/YYYY')}
                </p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`wrapped-tabpanel-${index}`}
      aria-labelledby={`wrapped-tab-${index}`}
      style={{ width: '100%' }}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `wrapped-tab-${index}`,
    'aria-controls': `wrapped-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    '&& header': {
      boxShadow: 'none',
      borderBottom: '2px solid #F5F6F8',
    },
  },
  tab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
    textTransform: 'none',
  },
  rootTab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
    color: '#000',
    textTransform: 'none',
  },
}));

const ServiceScreen = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState('one');
  const { Modal, show, hide } = useModal({ showed: false });

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleShowModalHistory = useCallback(() => {
    show();
  }, []);

  return (
    <div className="service-history-container">
      <Grid
        container
        style={{ backgroundColor: '#F6F9FF' }}
        className="history_service_wrapper"
      >
        <Breadcrumb items={[{ name: 'Lịch sử dịch vụ', url: '/history' }]} />
      </Grid>
      {/* <Grid container justifyContent="center">
        <Slider onClickSlider={() => {}} data={props.listBanner} />
      </Grid> */}
      <Grid style={{ marginTop: '20px' }} className="content">
        <Grid item lg={12} md={12} xs={12}>
          <TitleCustom title="Lịch sử dịch vụ" />
        </Grid>

        {/* App Tab */}
        <AppBar position="static" color="default">
          <Tabs
            value={value}
            onChange={handleChange}
            variant="scrollable"
            scrollButtons="on"
            textColor="primary"
            aria-label="scrollable force tabs example"
            className="app-bar"
          >
            <Tab
              classes={{
                selected: classes.tab,
                root: classes.rootTab,
              }}
              value="one"
              label="Voucher"
              wrapped
              {...a11yProps('one')}
            />
            <Tab
              classes={{
                selected: classes.tab,
                root: classes.rootTab,
              }}
              value="two"
              label="Mã yêu cầu"
              {...a11yProps('two')}
            />
            <Tab
              classes={{
                selected: classes.tab,
                root: classes.rootTab,
              }}
              value="three"
              label="Delivery"
              {...a11yProps('three')}
            />
            {/* <Tab value="three" label="Delivery" {...a11yProps('three')} /> */}
            <Grid className="box-history-voucher">
              <p onClick={handleShowModalHistory} className="text-history">
                Lịch sử sử dụng Voucher
              </p>
              <TimeICon className="icon" />
            </Grid>
          </Tabs>
        </AppBar>

        {/* Tab Voucher */}
        <TabPanel value={value} index="one">
          <VoucherHistory />
        </TabPanel>
        {/* Tab Mã yêu cầu */}

        <TabPanel value={value} index="two">
          <CodeRequired />
        </TabPanel>
        {/* Tab Delivery */}

        <TabPanel value={value} index="three">
          <DeliveryHistoryContainer />
        </TabPanel>
      </Grid>
      <Modal container className="box-button">
        <HistoryUseVoucherModal hideModal={hide} item />
      </Modal>
    </div>
  );
};

export default React.memo(ServiceScreen);
