import React from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import _ from 'lodash';
import './deliveryHistory.scss';
import DeliveryHistoryItem from '../../components/deliveryHistoryItem';
import DeliveryHistoryDetailModal from '../../components/deliveryHistoryDetailModal';

const DeliveryHistoryView = (props) => {
  const history = useHistory();
  return (
    <div className="delivery-history-container">
      <Grid container spacing={3}>
        {props.listDeliveryHistory &&
          props.listDeliveryHistory.map((obj) => (
            <Grid item lg={4} xl={4} xs={12} sm={12} md={6}>
              <DeliveryHistoryItem
                key={_.get(obj, '[0].deal_id')}
                data={obj}
                onClickDetail={(item) => {
                  history.push(
                    `/delivery/merchant/${item.branch?.merchant?.id}`
                  );
                }}
                onViewCodeClick={props.onViewCodeClick}
              />
            </Grid>
          ))}
      </Grid>
      <DeliveryHistoryDetailModal
        onCloseModalClick={props.onCloseModalClick}
        isOpen={props.isOpen}
        data={props.dataModal}
      />
    </div>
  );
};

export default DeliveryHistoryView;
