/* eslint-disable camelcase */
import React, { useState, useEffect, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import _ from 'lodash';
import {
  getListVoucherHistoryHandle,
  deleteVoucherItemHistoryHandle,
  useVoucherItemHistoryHandle,
} from 'src/redux/actions/voucher.action';
import VoucherHistory from './voucherHistory.view';

const VoucherHistoryContainer = () => {
  const dispatch = useDispatch();

  const [listVoucher, setListVoucher] = useState([]);

  // Get list voucher
  const callApiListVoucher = useCallback(() => {
    dispatch(
      getListVoucherHistoryHandle({
        onSuccess: (listVoucherRes) => {
          const dataNotUse = listVoucherRes.filter((obj) => {
            return obj.is_used === false;
          });
          const dataParser = _.groupBy(dataNotUse, 'deal_id');
          setListVoucher(Object.values(dataParser));
        },
      })
    );
  }, [dispatch]);

  // Use voucher

  // Event listen voucher
  useEffect(() => {
    callApiListVoucher();
  }, [callApiListVoucher]);

  const handleDeleteVoucher = useCallback(
    (itemVoucherDelete, hideModal) => {
      if (itemVoucherDelete.id) {
        dispatch(
          deleteVoucherItemHistoryHandle({
            voucherId: _.get(itemVoucherDelete, 'id'),
            onSuccess: () => {
              callApiListVoucher();
              hideModal();
            },
          })
        );
      }
    },
    [dispatch]
  );

  const handleUseVoucher = ({ voucherId, agent_code, onSuccess, onError }) => {
    dispatch(
      useVoucherItemHistoryHandle({
        voucherId,
        agent_code,
        onSuccess: () => {
          callApiListVoucher();
          onSuccess();
        },
        onError,
      })
    );
  };

  return (
    <VoucherHistory
      listVoucher={listVoucher}
      deleteVoucher={handleDeleteVoucher}
      fncUseVoucher={handleUseVoucher}
    />
  );
};

export default VoucherHistoryContainer;
