import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { getListDeliveryHistoryHandle } from 'src/redux/actions/delivery.action';
import DeliveryHistoryView from './deliveryHistory.view';

const DeliveryHistoryContainer = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [listDeliveryHistory, setListDeliveryHistory] = useState([]);
  const [dataModal, setDataModal] = useState({});
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(
      getListDeliveryHistoryHandle({
        page: 1,
        pageSize: 1000,
        onSuccess: (res) => {
          setListDeliveryHistory(res);
        },
      })
    );
  }, []);

  return (
    <DeliveryHistoryView
      onCloseModalClick={() => {
        setIsOpen(false);
      }}
      isOpen={isOpen}
      dataModal={dataModal}
      onViewCodeClick={(item) => {
        setDataModal(item);
        setIsOpen(true);
      }}
      listDeliveryHistory={listDeliveryHistory}
    />
  );
};

export default DeliveryHistoryContainer;
