import React, { useCallback, useState, useEffect, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import _ from 'lodash';
import moment from 'moment';
import QRCode from 'react-qr-code';

import { personVoucher } from 'src/assets/images';
import { useModal } from 'src/hooks';
import { ButtonCustom, ConfirmModalVoucher } from 'src/components/base';

import ItemInVoucherHistory from 'src/components/base/layout/ItemInVoucherHistory';
import ItemModalVoucherHistory from './components/itemModalVoucherHistory';
import './voucherHistory.scss';

const MODAL_ENUM = {
  MODAL_CONFIRM: 'MODAL_CONFIRM',
  MODAL_USE_VOUCHER: 'MODAL_USE_VOUCHER',
  MODAL_QR_CODE: 'MODAL_QR_CODE',
  MODAL_SUCCESS: 'MODAL_SUCCESS',
};

//----------------------------------------------------------------
/**
 * Screen: Voucher History
 */
const VoucherHistory = ({
  listVoucher,
  deleteVoucher = () => {},
  fncUseVoucher = () => {},
}) => {
  const { Modal, show, hide, isShow } = useModal({ showed: false });
  const [modalCurrent, setModalCurrent] = useState(
    MODAL_ENUM.MODAL_USE_VOUCHER
  );
  const [listDetailVoucher, setListDetailVoucher] = useState([]);
  const [voucherApply, setVoucherApply] = useState(null);
  const history = useHistory();
  const [useVoucherErrorMessage, setUseVoucherErrorMessage] = useState('');

  const [itemVoucherDelete, setItemVoucherDelete] = useState({});
  const handleShowModal = (elm) => {
    setListDetailVoucher(elm);
    setUseVoucherErrorMessage('');
    show();
  };

  // Event listen to set default modal
  useEffect(() => {
    if (isShow === false) {
      setModalCurrent(MODAL_ENUM.MODAL_USE_VOUCHER);
    }
  }, [isShow]);

  const renderModal = () => {
    const {
      MODAL_CONFIRM,
      MODAL_USE_VOUCHER,
      MODAL_QR_CODE,
      MODAL_SUCCESS,
    } = MODAL_ENUM;

    const handleModalConfirm = () => {
      deleteVoucher(itemVoucherDelete, hide);
    };

    switch (modalCurrent) {
      case MODAL_CONFIRM:
        return (
          <ConfirmModalVoucher
            isSuccess={false}
            onClickModal={handleModalConfirm}
            title=" Xác nhận xóa Voucher"
            des="Bạn chắc chắn mã này sử dụng và muốn xóa mã đã chọn?"
            buttonTitle="Xác nhận"
            item
            delete
          />
        );
      case MODAL_SUCCESS:
        return (
          <ConfirmModalVoucher
            isSuccess
            onClickModal={hide}
            title="Sử dụng Voucher thành công"
            des={`Bạn vừa sử dụng thành công ${voucherApply?.deal?.name}`}
            buttonTitle="Hoàn tất"
            item
            delete
          />
        );
      case MODAL_USE_VOUCHER:
        return (
          <UseVoucherModal
            title="XEM THÊM"
            hideModal={hide}
            listDetailVoucher={listDetailVoucher}
            changeStateModal={setModalCurrent}
            changeStateVoucherApply={setVoucherApply}
            deleteVoucher={(dataItemVoucher) => {
              setItemVoucherDelete(dataItemVoucher);
              setModalCurrent(MODAL_ENUM.MODAL_CONFIRM);
            }}
          />
        );
      case MODAL_QR_CODE:
        return (
          <QrCodeVoucherModal
            hideModal={hide}
            data={voucherApply}
            errorMessage={useVoucherErrorMessage}
            changeStateModal={setModalCurrent}
            fncUseVoucher={(agentCode) => {
              fncUseVoucher({
                voucherId: voucherApply.id,
                agent_code: agentCode,
                onSuccess: () => setModalCurrent(MODAL_ENUM.MODAL_SUCCESS),
                onError: () => {
                  setUseVoucherErrorMessage('Mã nhà cung cấp không đúng');
                },
              });
            }}
          />
        );
      default:
        return null;
    }
  };

  return (
    <div className="voucher-history-container">
      <Grid container spacing={3}>
        {listVoucher &&
          listVoucher.map((obj) => (
            <Grid item lg={4} xl={4} xs={12} sm={12} md={6}>
              <ItemInVoucherHistory
                key={_.get(obj, '[0].deal_id')}
                data={obj}
                onClickDetail={(item) => {
                  history.push(`/voucher/${item.deal_id}`);
                }}
                onClickUse={handleShowModal}
                onClickClose={() => {
                  console.log('onClickClose');
                }}
              />
            </Grid>
          ))}
      </Grid>
      <Modal container className="box-button">
        {renderModal()}
      </Modal>
    </div>
  );
};

//----------------------------------------------------------------
/**
 * Modal: detail voucher
 */
const UseVoucherModal = ({
  changeStateModal,
  listDetailVoucher,
  changeStateVoucherApply,
  deleteVoucher,
}) => {
  // add new element parser time -> dd/mm/yyyy
  const dataParser = listDetailVoucher.map((obj) => ({
    ...obj,
    create_at_parser: moment(_.get(obj, 'created_at')).format('DD/MM/YYYY'),
  }));

  const handleDeleteVoucher = useCallback(
    (itemVoucher) => {
      if (deleteVoucher && typeof deleteVoucher === 'function') {
        deleteVoucher(itemVoucher);
      }
    },
    [deleteVoucher]
  );

  /**
   * API not help Fe group by date
   * => FE group by group by here
   */
  const groupByDate = _.groupBy(dataParser, 'create_at_parser');
  const title = _.get(listDetailVoucher, '[0].deal.name');
  return (
    <div className="voucher-modal-container">
      <Grid container>
        <Grid container className="title">
          <p>{title}</p>
        </Grid>
        {Object.values(groupByDate).map((item) => (
          <ItemModalVoucherHistory
            key={item.id}
            data={item}
            onClickDeleteVoucher={handleDeleteVoucher}
            onClickUseVoucher={(voucher) => {
              changeStateVoucherApply(voucher);
              changeStateModal(MODAL_ENUM.MODAL_QR_CODE);
              // useVoucher()
            }}
          />
        ))}
      </Grid>
    </div>
  );
};

//----------------------------------------------------------------
/**
 * Modal: show scan qr-code
 */

const QrCodeVoucherModal = ({ errorMessage, data, fncUseVoucher }) => {
  const { code } = data;
  const agentCode = useRef('');

  return (
    <div className="use-voucher-container">
      <img className="img-person" alt="" src={personVoucher} />
      <p className="text-code">Mã voucher:</p>
      <h1>{code}</h1>
      <QRCode value={code} className="img-person" size={106} />
      <span style={{ marginBottom: '30px' }}>
        Để sử dụng voucher vui lòng chuyển máy cho nhân viên tại quầy thanh toán
        để nhập mã xác nhận từ nhà cung cấp.
      </span>
      <div className="error_message">{errorMessage}</div>
      <div className="box-input">
        <input
          item
          className="input-code"
          onChange={(elm) => {
            agentCode.current = elm.target.value;
          }}
          placeholder="Mã code của nhà cung cấp"
        />
        <ButtonCustom
          item
          title="Áp dụng"
          customStyle="button-apply"
          customTitle="button-apply-title"
          onClick={() => {
            if (fncUseVoucher && typeof fncUseVoucher === 'function') {
              fncUseVoucher(agentCode.current);
            }
          }}
        />
      </div>
    </div>
  );
};

export default React.memo(VoucherHistory);
