import React, { useCallback, useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import _ from 'lodash';
import moment from 'moment';
import QRCode from 'react-qr-code';

import {
  CirclePeople,
  CircleBaby,
  CircleClock,
  CircleTi,
} from 'src/assets/icons';

import { useModal } from 'src/hooks';
import { ButtonCustom, ConfirmModalVoucher } from 'src/components/base';

import ItemCodeRequiredHistory from 'src/components/base/layout/ItemCodeRequiredHistory';
import './codeRequired.scss';

// Define Enum of modal
const MODAL_ENUM = {
  MODAL_CONFIRM: 'MODAL_CONFIRM',
  MODAL_USE_VOUCHER: 'MODAL_USE_VOUCHER',
  MODAL_QR_CODE: 'MODAL_QR_CODE',
};

//----------------------------------------------------------------
/**
 * Screen: Voucher History
 */
const CodeRequired = ({ listCodeRequired }) => {
  console.log('listCodeRequired: ', listCodeRequired);
  const { Modal, show, hide, isShow } = useModal({ showed: true });
  const [modalCurrent, setModalCurrent] = useState(null);
  const [voucherApply, setVoucherApply] = useState({});
  const history = useHistory();

  const handleShowModal = useCallback((obj) => {
    setVoucherApply(obj);
    setModalCurrent(MODAL_ENUM.MODAL_QR_CODE);
    show();
  }, []);

  // Event listen to set default modal
  useEffect(() => {
    if (isShow === false) {
      setModalCurrent(MODAL_ENUM.MODAL_USE_VOUCHER);
    }
  }, [isShow]);

  if (parseInt(listCodeRequired.length) % 2 === 0) {
    listCodeRequired = [...listCodeRequired, {}];
  }

  const renderModal = () => {
    const { MODAL_CONFIRM, MODAL_QR_CODE } = MODAL_ENUM;
    switch (modalCurrent) {
      case MODAL_CONFIRM:
        return (
          <ConfirmModalVoucher
            isSuccess
            onClickModal={hide}
            title="Sử dụng Voucher thành công"
            des="Bạn vừa sử dụng thành công E-Voucher My Home 300.000đ"
            buttonTitle="Hoàn tất"
          />
        );
      case MODAL_QR_CODE:
        return (
          <QrCodeVoucherModal
            onHideModal={hide}
            setModalCurrent={setModalCurrent}
            data={voucherApply}
          />
        );
      default:
        return null;
    }
  };

  return (
    <div className="code-required-container">
      <div className="code-required-content">
        <Grid container spacing={3}>
          {listCodeRequired &&
            listCodeRequired.map((obj) => {
              // if (idx === 0) {
              return (
                <Grid item lg={4} xl={4} xs={12} sm={12} md={6}>
                  <ItemCodeRequiredHistory
                    key={_.get(obj, '[0].deal_id')}
                    data={obj}
                    onClickDetail={(item) => {
                      history.push(`/booking/${item.deal_id}`);
                    }}
                    onClickUse={() => handleShowModal(obj)}
                    onClickClose={() => {
                      console.log('onClickClose');
                    }}
                  />
                </Grid>
              );
              // }
            })}
        </Grid>
      </div>

      <Modal container className="box-button">
        {renderModal()}
      </Modal>
    </div>
  );
};

//----------------------------------------------------------------
/**
 * Modal: show scan qr-code
 */

const QrCodeVoucherModal = ({ data, onHideModal }) => {
  console.log('QrCodeVoucherModal>>>', data);
  const children_count = data?.children_count || 0;
  const infant_count = data?.infant_count || 0;
  const check_in_time = moment(_.get(data, 'check_in_time')).format('HH:mm');
  const created_at = moment(_.get(data, 'check_in_time')).format('DD/MM/yyyy');
  const name = _.get(data, 'branch.name');
  const adult_count = data?.adult_count || 0;
  const discount_price = _.get(data, 'discount_price');
  return (
    <div className="qr-code-modal-container">
      <p className="text-title-modal">Chi tiết đặt chỗ</p>
      <QRCode value={'code' || ''} className="img-person" size={106} />
      <h1 className="text-code-modal">{data?.code || ''}</h1>
      <Grid class="group-top-content">
        <Grid className="box-img">
          <img className="img" src={_.get(data, 'deal.image_urls[0]')} />
        </Grid>
        <Grid item lg={10} md={10} sm={10} className="box-content">
          {/* Need clear field  My */}
          <p className="text-shop-name">{data.deal.name}</p>
          <div className="box-des">
            <p className="text-name">{name}</p>
            <p className="text-sale">
              {discount_price ? `Giảm ${discount_price}%` : ''}
            </p>
          </div>
        </Grid>
      </Grid>
      <Grid className="group-center-content">
        <Grid>
          <p className="address">{data?.branch?.address || ''}</p>
          <p className="booked">Đã đặt: {created_at}</p>
          <Grid className="info">
            <Grid className="box-icon">
              <CirclePeople className="icon" />
              <span>{adult_count} người</span>
            </Grid>
            <Grid className="box-icon">
              <CircleBaby className="icon" />
              <span>{infant_count} em bé</span>
            </Grid>
          </Grid>
          <Grid className="info">
            <Grid className="box-icon">
              <CircleTi className="icon" />
              <span>{children_count} trẻ em</span>
            </Grid>
            <Grid className="box-icon">
              <CircleClock className="icon" />
              <span>{check_in_time}</span>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      {data.note && (
        <>
          <p className="title-input">Ghi chú:</p>
          <input item className="inputCode" value={data.note || ''} />
        </>
      )}
      <ButtonCustom
        item
        title="Đóng"
        customStyle="button-cancel"
        customTitle="button-cancel-title"
        onClick={onHideModal}
      />
    </div>
  );
};

export default React.memo(CodeRequired);
