/* eslint-disable array-callback-return */
/* eslint-disable consistent-return */
/* eslint-disable camelcase */
import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import _ from 'lodash';
import moment from 'moment';
import { ButtonCustom } from 'src/components/base';
import './deliveryHistoryItem.scss';
import { BookingDateIcon, DishesIcon, MoneyIcon } from 'src/assets/icons';
import { convertCurrency } from 'src/utils/currency';

const DeliveryHistoryItem = React.memo((props) => {
  const { data, onClickDetail = () => {} } = props;

  const handleClickDetail = useCallback(() => {
    if (onClickDetail && typeof onClickDetail === 'function') {
      onClickDetail(data);
    }
  }, [onClickDetail]);

  /**
   * Check object is empty => create new space white
   */
  if (_.isEmpty(data)) {
    return <Grid className="box-empty" />;
  }

  const created_at = moment(_.get(data, 'check_in_time')).format('DD/MM/yyyy');
  const name = _.get(data, 'branch.merchant.name');
  const subName = _.get(data, 'branch.name');
  const address = _.get(data, 'address');
  return (
    <div className="item-delivery-history">
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'flex-end',
        }}
      >
        {data.delivery_status === 0 ? (
          <div
            style={{
              width: '200px',
              justifyContent: 'center',
              display: 'flex',
              padding: '4px 8px',
              borderRadius: '0 0 0 4px',
              backgroundColor: '#37C86F',
              fontFamily: 'Google Sans',
              fontWeight: 400,
              fontStyle: 'italic',
              fontSize: '1rem',
              color: 'white',
            }}
          >
            Đặt hàng thành công
          </div>
        ) : (
          <div
            style={{
              width: '200px',
              justifyContent: 'center',
              display: 'flex',
              padding: '4px 8px',
              borderRadius: '0 0 0 4px',
              fontFamily: 'Google Sans',
              fontWeight: 400,
              fontStyle: 'italic',
              fontSize: '1rem',
              color: 'white',
              backgroundColor: '#FE5466',
            }}
          >
            Đã hủy đơn
          </div>
        )}
      </div>
      <Grid container className="item_delivery_history_content">
        {/* Top */}
        <div className="group-top-content">
          <div className="box-img">
            <img
              className="img"
              alt=""
              src={_.get(data, 'branch.merchant.image_url')}
            />
          </div>
          <div className="box-content">
            {/* Need clear field  My */}
            <h1 className="deal_name">{name}</h1>
            <p className="merchant-name">{subName}</p>
          </div>
        </div>

        {/* Center */}
        <Grid className="group-center-content">
          <Grid>
            <p className="booked">
              Đã đặt:
              {created_at}
            </p>
            <p className="address">{address} </p>

            <div className="info">
              <div className="item_content">
                <BookingDateIcon className="receive_time_icon icon" />

                <div className="receive_time_title item_content_title">
                  Thời gian nhận:
                </div>
                <div className="receive_time_content item_content_data">
                  {`${data.shipping_time} - ${data.shipping_date}`}
                </div>
              </div>
            </div>
            <div className="item_content">
              <DishesIcon className="icon" />
              <div className="receive_time_title item_content_title">
                Số phần:
              </div>
              <div className="receive_time_content item_content_data">
                {data?.deals?.reduce(
                  (total, current) => total + current.quantity,
                  0
                )}
              </div>
            </div>
            <div className="item_content">
              <MoneyIcon className="icon" />
              <div className="total_money_title item_content_title">
                Tổng tiền:
              </div>
              <div className="item_content_data">
                {convertCurrency(
                  data?.deals?.reduce(
                    (total, current) =>
                      total + current.quantity * current?.deal?.regular_price,
                    0
                  )
                )}{' '}
                đ
              </div>
            </div>
          </Grid>
        </Grid>

        {/* Bottom */}
        <Grid className="group-bottom-button">
          <ButtonCustom
            title="Mã đặt món"
            customStyle="button-use"
            customTitle="button-use-title"
            onClick={() => {
              if (typeof props.onViewCodeClick === 'function') {
                props.onViewCodeClick(data);
              }
            }}
          />
          <ButtonCustom
            title="Chi tiết deal"
            customStyle="button-detail"
            customTitle="button-detail-title"
            onClick={handleClickDetail}
          />
        </Grid>
      </Grid>
    </div>
  );
});
export default React.memo(DeliveryHistoryItem);
