/* eslint-disable array-callback-return */
/* eslint-disable consistent-return */
/* eslint-disable camelcase */
import React from 'react';
import _ from 'lodash';
import moment from 'moment';
import './style.scss';
import { ClockIcon, DishesIcon, NoteIcon } from 'src/assets/icons';
import { Modal } from '@material-ui/core';
import { convertCurrency } from 'src/utils/currency';
import QRCode from 'react-qr-code';
import { IconButton } from 'src/components/base';

const DeliveryHistoryDetailModal = (props) => {
  const { data } = props;

  /**
   * Check object is empty => create new space white
   */
  console.log('data>>>>', props.data);
  const created_at = moment(_.get(data, 'check_in_time')).format('DD/MM/yyyy');
  const merchantName = _.get(data, 'branch.merchant.name');
  const address = _.get(data, 'address');
  return (
    <Modal open={props.isOpen} className="delivery_history_detail_modal">
      <div className="delivery_history_detail_modal_container">
        <div className="modal-header">
          <IconButton
            className="icon_close"
            icon="close"
            onClick={props.onCloseModalClick}
          />
        </div>
        <div
          style={{ display: 'flex', justifyContent: 'center' }}
          className="title"
        >
          Chi tiết đặt món
        </div>
        <div className="delivery_history_detail_modal_content">
          <div className="delivery_info_container">
            <div className="info_merchant_container">
              <img
                className="image_merchant"
                alt=""
                src={_.get(data, 'branch.merchant.image_url')}
              />

              <div className="merchant_name">{merchantName}</div>
            </div>

            {/* Center */}
            <div className="delivery_address_container">
              <p className="address">{address} </p>
              <p className="booking_date">
                Đã đặt:
                {created_at}
              </p>
              <div className="delivery_info_content_item">
                <ClockIcon className="receive_time_icon icon" />

                <div className="receive_time_title item_content_title">
                  Thời gian nhận:
                </div>
                <div className="receive_time_content">
                  {`${data.shipping_time} - ${data.shipping_date}`}
                </div>
              </div>
              {data.note && (
                <>
                  <div className="delivery_info_content_item">
                    <NoteIcon className="icon" />

                    <div className="note_title">Ghi chú:</div>
                  </div>
                  <div className="note_content">{data.note}</div>
                </>
              )}
            </div>

            <div className="delivery_food_container">
              <div className="delivery_food_title_container">
                <DishesIcon className="icon" />
                <div className="delivery_food_title">Đơn hàng</div>
              </div>
              {data.deals?.map((obj) => {
                return (
                  <div className="food_container">
                    <img
                      className="food_image"
                      alt=""
                      src={obj.deal.image_urls?.[0]}
                    />
                    <p className="food_quantity">x {obj.quantity}</p>
                    <p className="food_name">{obj.deal.name}</p>
                    <p className="food_price">
                      {convertCurrency(obj.deal.regular_price * obj.quantity)}đ
                    </p>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="divider_line" />
          <div className="qr_code_container">
            <div className="qr_code_content">
              <div className="qr_code_title">Mã đặt món</div>
              <QRCode value={data.code} className="img-person" size={106} />
              <div className="qr_code_value">{data.code}</div>
              <div
                style={{
                  width: '100%',
                  display: 'flex',
                  justifyContent: 'center',
                }}
              >
                {data.delivery_status === 0 ? (
                  <div
                    style={{
                      width: '200px',
                      justifyContent: 'center',
                      display: 'flex',
                      padding: '4px 8px',
                      borderRadius: '0 0 0 4px',
                      backgroundColor: '#37C86F',
                      fontFamily: 'Google Sans',
                      fontWeight: 400,
                      fontStyle: 'italic',
                      fontSize: '1rem',
                      color: 'white',
                    }}
                  >
                    Đặt hàng thành công
                  </div>
                ) : (
                  <div
                    style={{
                      width: '200px',
                      justifyContent: 'center',
                      display: 'flex',
                      padding: '4px 8px',
                      borderRadius: '0 0 0 4px',
                      fontFamily: 'Google Sans',
                      fontWeight: 400,
                      fontStyle: 'italic',
                      fontSize: '1rem',
                      color: 'white',
                      backgroundColor: '#FE5466',
                    }}
                  >
                    Đã hủy đơn
                  </div>
                )}
              </div>
            </div>
            <div className="delivery_money_container">
              <div className="temporary_fee_container">
                <div style={{ display: 'flex' }}>
                  <div className="temporary_fee_title">Tạm tính</div>
                  <div className="temporary_fee_food_quantity">
                    (
                    {data?.deals?.reduce(
                      (total, current) => total + current.quantity,
                      0
                    )}{' '}
                    phần)
                  </div>
                </div>
                <div className="temporary_fee_content">
                  {convertCurrency(
                    data?.deals?.reduce(
                      (total, current) =>
                        total + current.quantity * current?.deal?.regular_price,
                      0
                    )
                  )}{' '}
                  đ
                </div>
              </div>
              <div className="shipping_fee_container">
                <div style={{ display: 'flex' }}>
                  <div className="delivery_fee_title">Phí giao hàng</div>
                  <div className="delivery_distance">
                    ({data.shipping_distance} km)
                  </div>
                </div>
                <div className="delivery_distance_content">
                  {convertCurrency(data.shipping_price)} đ
                </div>
              </div>
              {data.promotion_code && (
                <div className="promotion_container">
                  <div style={{ display: 'flex' }}>
                    <div className="promotion_title">Mã giảm giá</div>
                    <div className="promotion_code">
                      ({data.promotion_code})
                    </div>
                  </div>
                  <div className="promotion_discount_content">
                    {data.total_price_discount}
                  </div>
                </div>
              )}
              <div className="diver_fee_line" />
              <div className="total_money_container">
                <div className="temporary_fee_title">Tổng tiền</div>
                <div className="temporary_fee_content">
                  {convertCurrency(data.total_money)} đ
                </div>
              </div>
              <div className="payment_type_container">
                <div className="payment_type_title">Hình thức thanh toán</div>
                <div className="payment_type_content">Thanh toán điểm</div>
              </div>
            </div>
            {/* Bottom */}
            <div className="group-bottom-button">
              <div
                className="reorder_delivery_button"
                // onClick={handleClickUse}
              >
                Đặt lại
              </div>
              <div
                className="cancel_delivery_button"
                // onClick={handleClickUse}
              >
                Hủy đơn
              </div>
            </div>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default DeliveryHistoryDetailModal;
