/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable camelcase */
import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { ButtonCustom } from 'src/components/base';
import _ from 'lodash';
import moment from 'moment';

import {
  CirclePeople,
  CircleBaby,
  CircleClock,
  CircleTi,
} from 'src/assets/icons';
import './styles.scss';

function ItemCodeRequiredHistory(props) {
  const { data, onClickDetail = () => {}, onClickUse = () => {} } = props;

  const handleClickUse = useCallback(() => {
    if (onClickUse && typeof onClickUse === 'function') {
      onClickUse(data);
    }
  }, [onClickUse]);

  const handleClickDetail = useCallback(() => {
    if (onClickDetail && typeof onClickDetail === 'function') {
      onClickDetail(data);
    }
  }, [onClickDetail]);

  /**
   * Check object is empty => create new space white
   */
  if (_.isEmpty(data)) {
    return <Grid className="box-empty" />;
  }

  const children_count = data?.children_count || 0;
  const infant_count = data?.infant_count || 0;
  const check_in_time = moment(_.get(data, 'check_in_time')).format('HH:mm');
  const created_at = moment(_.get(data, 'check_in_time')).format('DD/MM/yyyy');
  const name = _.get(data, 'deal.name');
  const subName = _.get(data, 'branch.name');
  const address = _.get(data, 'branch.name');
  const adult_count = data?.adult_count || 0;

  return (
    <div className="item-code-required">
      <Grid container>
        {/* Top */}
        <div className="group-top-content">
          <div className="box-img">
            <img
              className="img"
              alt=""
              src={_.get(data, 'deal.merchant.image_url')}
            />
          </div>
          <div className="box-content">
            {/* Need clear field  My */}
            <h1>{name}</h1>
            <p className="text-name">{subName}</p>
          </div>
        </div>

        {/* Center */}
        <Grid className="group-center-content">
          <Grid>
            <p className="address">{address} </p>
            <p className="booked">
              Đã đặt:
              {created_at}
            </p>
            <Grid className="info">
              <Grid className="box-icon">
                <CirclePeople />
                <span>{adult_count} người</span>
              </Grid>
              <Grid className="box-icon">
                <CircleBaby />
                <span>{infant_count} em bé</span>
              </Grid>
            </Grid>
            <Grid className="info">
              <Grid className="box-icon">
                <CircleTi />
                <span>{children_count} trẻ em</span>
              </Grid>
              <Grid className="box-icon">
                <CircleClock />
                <span>{check_in_time}</span>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        {/* Bottom */}
        <Grid className="group-bottom-button">
          <ButtonCustom
            title="Chi tiết đặt chỗ"
            customStyle="button-use"
            customTitle="button-use-title"
            onClick={handleClickUse}
          />
          <ButtonCustom
            title="Chi tiết deal"
            customStyle="button-detail"
            customTitle="button-detail-title"
            onClick={handleClickDetail}
          />
        </Grid>
      </Grid>
    </div>
  );
}

ItemCodeRequiredHistory.propTypes = {
  onClickDetail: PropTypes.func.isRequired,
  onClickUse: PropTypes.func.isRequired,
};

export default React.memo(ItemCodeRequiredHistory);
