import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { ButtonCustom } from 'src/components/base';
import _ from 'lodash';

import './styles.scss';

function ItemInformation(props) {
  const { data, onClickDetail = () => {}, onClickUse = () => {} } = props;

  const handleClickUse = useCallback(() => {
    if (onClickUse && typeof onClickUse === 'function') {
      onClickUse(data);
    }
  }, [onClickUse]);

  const handleClickDetail = useCallback(() => {
    if (onClickDetail && typeof onClickDetail === 'function') {
      onClickDetail(data[0]);
    }
  }, [onClickDetail]);

  /**
   * Check object is empty => create new space white
   */
  if (_.isEmpty(data)) {
    return <Grid className="box-empty" />;
  }

  const item = _.get(data, '[0].deal');

  return (
    <div className="item-voucher">
      <div className="group-top-content">
        <div className="box-img">
          <Grid className="badge">
            <p>{data.length}</p>
          </Grid>
          <img className="img" alt="" src={_.get(item, 'merchant.image_url')} />
        </div>
        <div className="box-content">
          {/* Need clear field */}
          <h1 className="title">My Home</h1>
          <p className="voucher_name">{_.get(item, 'name')}</p>
        </div>
      </div>
      <div className="group-bottom-button">
        <ButtonCustom
          title="Sử dụng"
          customStyle="button-use"
          customTitle="button-use-title"
          onClick={handleClickUse}
        />
        <ButtonCustom
          title="Chi tiết deal"
          customStyle="button-detail"
          customTitle="button-detail-title"
          onClick={handleClickDetail}
        />
      </div>
    </div>
  );
}

ItemInformation.propTypes = {
  onClickDetail: PropTypes.func.isRequired,
  onClickUse: PropTypes.func.isRequired,
};

export default React.memo(ItemInformation);
